import Vue from 'vue'
import Vuex from 'vuex'
import clone from "@/lib/clone";
import generateId from "@/lib/idGenerator";
import router from "@/router";

Vue.use(Vuex)

const store = new Vuex.Store({

  state: {
    recordList: [],
    tagList: [],
    currentEditingTag: undefined
  } as RootState,

  mutations: {

    fetchRecords(state) {
      state.recordList = JSON.parse(window.localStorage.getItem('recordList') || '[]') as RecordItem[]
    },

    createRecord(state, record: RecordItem) {
      const deepClone: RecordItem = clone(record)
      deepClone.recordedAt = new Date().toISOString()
      state.recordList.push(deepClone)
      store.commit('saveRecords')
      window.alert('添加成功')
    },

    saveRecords(state) {
      window.localStorage.setItem('recordList', JSON.stringify(state.recordList))
    },

    fetchTags(state) {
      state.tagList = JSON.parse(window.localStorage.getItem('tagList') || '[]') as Tag[]
      if (!state.tagList || state.tagList.length === 0) {
        ['衣', '食', '住', '行'].forEach(itemName => {
          store.commit('createTag', itemName)
        })
        // store.commit('createTag', '衣')
        // store.commit('createTag', '食')
        // store.commit('createTag', '住')
        // store.commit('createTag', '行')
      }
    },

    findTag(state, id: string) {
      state.currentEditingTag = state.tagList.filter(item => item.id === id)[0]
    },

    createTag(state, newTagName: string) {

      const nameArr = state.tagList.map(item => item.name)

      if (nameArr.indexOf(newTagName) >= 0) {
        window.alert('已存在相同标签')
        return
      }

      const id = generateId().toString()
      state.tagList.push({id: id, name: newTagName})
      store.commit('saveTags')
      // window.alert('添加成功')
    },

    saveTags(state) {
      window.localStorage.setItem('tagList', JSON.stringify(state.tagList))
    },

    removeTag(state, id: string) {
      let index = -1
      for (let i = 0; i < state.tagList.length; i++) {
        if (state.tagList[i].id === id) {
          index = i
          break
        }
      }
      if (index >= 0) {
        state.tagList.splice(index, 1)
        store.commit('saveTags')
        router.back()
        window.alert('删除成功')
      } else {
        window.alert('删除失败，标签不存在')
      }
    },

    updateTag(state, payload: {id: string, name: string}) {
      const idArr = state.tagList.map(item => item.id)
      const nameArr = state.tagList.map(item => item.name)
      if (idArr.indexOf(payload.id) >= 0) {
        if (nameArr.indexOf(payload.name) >= 0) {
          window.alert('已存在相同标签名')
          // return 'repeated name'
        } else {
          const t = state.tagList.filter(item => item.id === payload.id)[0]
          t.name = payload.name
          store.commit('saveTags')
          window.alert('标签名更改成功')
          // return 'success'
        }
      } else {
        window.alert('没有找到要更改的标签')
        // return 'not found'
      }
    }
  },
  actions: {
  },
  modules: {
  }
})

export default store
