import Vue from "vue";
import Component from "vue-class-component";

@Component
export default class TagHelper extends Vue {
  createTag() {
    const newTagName = window.prompt('请输入标签名')
    if (newTagName) {
      this.$store.commit('createTag', newTagName)
    } else {
      alert('标签名不能为空')
    }
  }
}