
































import Vue from "vue";
import {Component} from "vue-property-decorator";
import Tabs from "@/components/Tabs.vue";
// import durationsList from "@/constants/durationsList";
import typesList from "@/constants/typesList";
import dayjs from "dayjs"
import clone from '@/lib/clone';

@Component({
  components: {Tabs}
})
export default class Statistics extends Vue {

  typesList = typesList

  typeValue: string = '-'

  // durationsList = durationsList
  //
  // durationValue: string = 'byDay'

  get recordList() {
    return (this.$store.state as RootState).recordList
  }

  get resultList() {

    const {recordList} = this

    if (recordList.length === 0) {return []}

    const sortedRecordList = clone(recordList).filter((item) => item.type === this.typeValue).sort((a, b) =>
      (dayjs(b.recordedAt).valueOf() - dayjs(a.recordedAt).valueOf())
    )

    type GroupedRecordList = {
      title: string,
      items: RecordItem[],
      total?: number
    }[]

    if (sortedRecordList.length === 0) {return [] as GroupedRecordList}

    const groupedRecordList: GroupedRecordList = [{title: dayjs(sortedRecordList[0].recordedAt).format('YYYY-MM-DD'), items: [sortedRecordList[0]]}]

    for (let i = 1; i < sortedRecordList.length; i++) {
      const current = sortedRecordList[i]
      const last = groupedRecordList[groupedRecordList.length - 1]

      if (dayjs(current.recordedAt).isSame(dayjs(last.title), 'day')) {
        last.items.push(current)
      } else {
        groupedRecordList.push({title: dayjs(current.recordedAt).format('YYYY-MM-DD'), items: [current]})
      }
    }

    groupedRecordList.forEach(group => {
      group.total = group.items.reduce((sum, item) => sum + item.numberOutput, 0)
    })

    return groupedRecordList
  }

  beforeCreate() {
    this.$store.commit('fetchRecords')
  }


  tagString(tags: Tag[]) {
    return tags.length === 0 ? '无' : tags.map(t => t.name).join('，')
  }

  formatTitle(title: string) {
    const titleDay = dayjs(title)
    const now = dayjs()
    if (titleDay.isSame(now, 'day')) {
      return '今天'
    } else if (titleDay.isSame(now.subtract(1, 'day'), 'day')) {
      return '昨天'
    } else if (titleDay.isSame(now.subtract(2, 'day'), 'day')) {
      return '前天'
    } else if(titleDay.isSame(now, 'year')) {
      return titleDay.format('M月D日')
    } else {
      return titleDay.format('YYYY年M月D日')
    }
  }

}
