





















import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import NoteInput from "@/components/Money/NoteInput.vue";
import ReuseBtn from "@/components/ReuseBtn.vue";

@Component({
  components: {ReuseBtn, NoteInput}
})
export default class EditLabel extends Vue {

  get localTag() {
    return this.$store.state.currentEditingTag
  }

  created() {
    this.$store.commit('fetchTags')  // 不能默认tagList存在，否则会导致currentEditingTag为undefined，使得下一行代码报错
    this.$store.commit('findTag', this.$route.params.id)
    if (!this.localTag) {
      this.$router.replace('/404')
    }
  }

  updateTag(name: string) {
    if (this.localTag) {
      this.$store.commit('updateTag', {id: this.localTag.id, name: name})
    }
  }

  removeTag() {
    if (this.localTag) {
      this.$store.commit('removeTag', this.localTag.id)
    }
  }

  // goBack() {
  //   this.$router.back()
  // }

}
